<template>
  <el-container class="ard">
    <Header title="文章详情"/>
    <el-main class="ard-main cm-vertical">
      <h3>{{article.title}}</h3>
      <aside class="cm-horizon ard-info cm-padding-v">
        <el-link type="primary">{{articleType}}</el-link>
        <span class="cm-padding-h">{{dateFormat(article.updatedAt)}}</span>
        <el-link type="primary" @click="$linkTo(`/ArticleAddOrEdit/${article.type}/${article.objectId}`)" v-if="admin">编辑</el-link>
      </aside>
      <article class="ard-content cm-horizon cm-flex cm-full-w">
        <div v-html="article.content"></div>
      </article>
    </el-main>
    <el-footer>
      <el-button-group class="cm-full-w cm-horizon">
        <el-button class="cm-flex">Like</el-button>
        <el-button class="cm-flex">Comment</el-button>
      </el-button-group>
    </el-footer>
  </el-container>
</template>

<script>
  import {AV} from '../../common/api';
  import {formatDate} from '../../common/utils';
  import Model from '../../model/';
  export default {
    components: {}, computed: {
      articleType(){
        return Model['articleType'][this.article.type];
      }
    },
    methods: {
      getArticleDetail(){
        const {objectId} = this.$route.params;
        const loading = this.$loading(this.$loadingConfig)
        AV.getData('articleList', objectId).then(res => {
          this.article = res;
        }).catch(e => {
          this.$alert('提交失败,' + e);
        }).finally(() => {
          loading.close()
        })
      },
      isAdmin(){
        AV.isAdmin().then((is) => {
          this.admin = is;
        })
      },
      dateFormat(date){
        return formatDate(date, false);
      }
    },
    data(){
      return {
        article: {},
        admin: false,
      }
    },
    created(){

    },
    mounted(){
      this.getArticleDetail();
      this.isAdmin();
    },
  }
</script>

<style>
  .ard {
  }

  .ard-info {
    width: 100%;
    font-size: var(--font-sm);
    color: grey;
  }

  .ard-content {
    padding: 10px;
    text-align: left;
    background-color: #ebeef5;
  }

  .ard-content p {
    text-indent: 20px;
  }

  .ard-main {
    align-items: flex-start;
    width: 100%;
  }
</style>



